html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  cursor: pointer;
}

button:hover {
  opacity: 0.5;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  background-color: rgb(237, 237, 237);
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}

h1 {
  font-weight: 700;
  font-size: 61px;
}

h2 {
  font-size: 48px;
  font-weight: 400;
}

h3 {
  font-size: 39px;
  font-weight: 400;
  line-height: 53px;
}

h4 {
  font-size: 31px;
  font-weight: 400;
  line-height: 43px;
}

.link {
  width: 203px;
  margin-top: 58px;
  margin-bottom: 24px;
}

.link-cart {
  width: 203px;
  margin-top: 56px;
  margin-bottom: 40px;
}

.link__wrapper {
  display: flex;
  justify-content: space-between;
}

.img__vector {
  height: 24px;
}

.link__title {
  font-size: 20px;
  line-height: 27px;
}
