@import url("./reset.css");

.order {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-top: 48px;
}

.order__info {
  display: flex;
  gap: 97px;
}

.order__wrapper,
.order__bag-list {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
}

.order__info-address,
.order__payment-method,
.order__bag-wrapper {
  width: 871px;
  padding: 24px;
  border-radius: 13px;
  background: rgb(255, 255, 255);
}

.address__wrapper,
.payment__wrapper {
  margin-top: 8px;
}

.address__info-form {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  row-gap: 8px;
  font-size: 20px;
  line-height: 27px;
}

.payment__info {
  display: flex;
  align-items: center;
  gap: 8px;
}

.payment__img {
  width: 24px;
  height: 24px;
}

.payment__text {
  font-family: Cabin;
  font-size: 20px;
  line-height: 24px;
}

.bag__card-title {
  display: flex;
  gap: 16px;
}

.bag__card-img {
  width: 203px;
}

.bag__series {
  margin-top: 8px;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 27px;
  color: rgb(96, 105, 92);
}

.bag__description {
  margin-bottom: 24px;
  font-size: 20px;
  line-height: 27px;
  color: rgb(26, 31, 22);
}

.img-rating {
  margin-bottom: 26px;
}

.bag__card-price {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}

.price {
  font-size: 20px;
  line-height: 27px;
}

.price__currency {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.total__order {
  width: 243px;
  height: 194px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
  border-radius: 12px;
  background: rgb(255, 255, 255);
}

.total__wrapper {
  display: flex;
  justify-content: space-between;
}

.total__title {
  font-size: 20px;
  line-height: 27px;
}

.total__wrapper-text,
.total__final-text,
.total-final__usd,
.delivery__text,
.total-text__usd {
  font-family: Cabin;
  font-size: 16px;
  line-height: 22px;
  color: rgb(96, 105, 92);
}

.total__final-text,
.total-final__usd {
  color: rgb(229, 37, 44);
}
